*{
    font-family: inherit;
}

input, a, button{
    font-family: 'frt', sans-serif;

    &:focus{
        outline: none;
        text-decoration: none;
    }

    &:hover{
        text-decoration: none;
    }
}

button{
    cursor: pointer;

    &:focus, &:active{
        outline: none;
        box-shadow: none;
    }
}

ul.res{
    padding: 0;
    margin: 0;
    list-style: none;
}