@import 'main';
// End Of Import

// Side Nav

.navover{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .8);
    z-index: 99;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideNav{
    padding: 24px;
    position: fixed;
    top: 0;
    right: -261px;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow: auto;
    transition: .5s;

    &.open {
        right: 0;
    }

    .opt{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang{


            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                background-color: #84848445;
                color: #888;
                font-size: 15px;
            }
        }

        .xtr{
            position: relative;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 10px;

            form{
                opacity: 0;
                visibility: hidden;
                z-index: 99;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(109, 108, 108, 0.9);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                display: none;

                @include Sm{
                    display: flex;
                }

                & > i{
                    position: absolute;
                    top: 10%;
                    color: #d6d6d6;
                    font-size: 25px;
                    cursor: pointer;
                    transition: .5s;
                }

                &.open{
                    opacity: 1;
                    visibility: visible;

                    .cont{
                        opacity: 1;
                        visibility: visible;
                    }
                }

                .cont{
                    position: relative;
                    visibility: hidden;
                    opacity: 0;
                    transition: .5s;
                    top: -20%;
                }

                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;
                    position: absolute;
                    left: 10px;
                    top: 50%;
                    transform: translateY(-45%);
                    margin-left: 10px;
                    width: 30px;
                    height: 30px;

                    i{
                        font-size: 20px;
                        color: rgb(109, 108, 108);
                    }
                }

                input{
                    border: 0;
                    border-radius: 100px;
                    width: 500px;
                    height: 45px;
                    padding: 0 25px;
                    background-color: $color-white;
                    // display: none;

                    @include Md{
                        width: 500px;
                    }
        
                    @include Xs{
                        width: 400px;
                    }
        
                    @include mob480{
                        width: 300px;
                    }
                }
            }
        }

        .close1{
            font-size: 20px;
            color: #84848445;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links{
        display: block;

        .list{
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;

            & > li{
                padding: 7px 15px;
                width: 100%;

                a{
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: #6c6b6b;
                    line-height: 1.6;
                    transition: .5s;                

                    &:hover{
                        color: $color-black;
                    }
                }

                &:not(:last-child){
                    border-bottom: 1px solid #dedede;
                }
            }
        }
    }
}

// Side Nav

// Start Navbar
.navBar{
    padding: 13px 0;
    background-color: #e9e9e9;
    transition: .5s;

    @include Sm{
        padding: 20px 0;
    }

    &.scroll{
        padding: 5px 0;
        position: sticky;
        top: 0;
        z-index: 999;
        box-shadow: 0px 0px 10px 0px rgba(22, 22, 22, 0.1);
        transition: .5s;
    }

    &.hide{
        // transform: translateY(-100px);
        top: -100%;
    }

    .navCon{
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;

        .logo{

            @include mob480{
                img{
                    position: relative;
                    right: 20px;
                }
            }

            .icon{
                position: absolute;
                top: 50%;
                left: 20px;
                transform: translateY(-50%);
                cursor: pointer;
                display: none;
                font-size: 30px;
                color: #6c6b6b;

                @include Sm{
                    display: block;
                }
            }

            .srch{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: #84848445;
                cursor: pointer;
                position: absolute;
                top: 48%;
                left: 60px;
                width: 40px;
                height: 40px;
                transform: translateY(-50%);
                background-color: transparent;
                display: none;

                i{
                    color: #6c6b6b;
                    font-size: 25px;
                    transition: .5s;
                }

                @include Sm{
                    display: flex;
                }
            }
        }
    
        .links{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-grow: 1;
            margin: 0 auto;

            @include Sm{
                display: none;
            }

            & > ul{
                display: flex;
                margin-right: auto;

                li{

                    &:not(:last-child){
                        @include Md{
                            margin-left: 20px;
                        }
            
                        @include Lg{
                            margin-left: 34px;
                        }
                    }
                    
                    a{
                        font-size: 17px;
                        color: $color-prim;
                        transition: .5s;
    
                        &:hover{
                            color: #000;
                        }
                    }
                }
            }

            .xtr{
                display: flex;
                margin-right: auto;

                & *{
                    color: #6d6c6c;
                }

                .lang, .srch{
                    width: 36px;
                    height: 36px;
                    background-color: $color-white;
                    // box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: .5s;
                    cursor: pointer;

                    & *{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 14px;
                        transition: .5s;
                    }

                    &:hover{
                        background-color: $color-prim;

                        i, a{
                            color: $color-white;
                        }
                    }
                }

                .srch{
                    margin-left: 10px;

                    i{
                        font-size: 13px;
                    }
                }

                form{
                    opacity: 0;
                    visibility: hidden;
                    display: none;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: max-content;
                    height: max-content;
                    position: fixed;
                    top: 0;
                    right: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(#6d6c6c, .9);
                    transition: .5s;
                    z-index: 9;

                    &.open{
                        opacity: 1;
                        visibility: visible;

                        button{
                            position: absolute;
                            left: 0;
                            top: 50%;
                            transform: translateY(-45%);
                            margin-left: 15px;
                            width: 30px;
                            height: 30px;
                        }

                        input{
                            display: block;
                        }
                    }

                    .cont{
                        position: relative;
                        width: 50%;

                        @include Md{
                            width: 50%;
                        }
            
                        // @include Lg{
                        //     width: 555px;
                        // }
            
                        @include Xs{
                            width: 70%;
                        }
            
                        @include mob480{
                            width: 90%;
                        }
                    }

                    button{
                        width: 100%;
                        height: 100%;
                        background-color: transparent;
                        border: 0;

                        i{
                            font-size: 18px;
                        }
                    }

                    input{
                        border: 0;
                        border-radius: 100px;
                        width: 100%;
                        height: 45px;
                        padding: 0 25px;
                        background-color: $color-white;
                        display: none;
                    }

                    & > i{
                        position: absolute;
                        top: 25%;
                        color: #d6d6d6;
                        font-size: 25px;
                        cursor: pointer;
                    }
                }

            }
        }
    }
}
// End of Navbar

// Slider Settings

.owl-nav{
    position: absolute;
    top: 49%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 100px;

    @include Sm{
        display: none;
    }

    button{

        &.owl-prev{
            span{
                border-left: 0;
                border-bottom: 0;
            }
        }

        &.owl-next{
            span{
                border-right: 0;
                border-top: 0;
            }
        }

        span{
            display: block;
            width: 15px;
            height: 15px;
            border: 1px solid $color-prim;
            transform: rotate(45deg);
        }
    }
}

// Slider Styles

.owl-dots{
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs{
        bottom: 15px;
    }
    
    button{

        &:not(:last-child){
            margin-left: 3px;
        }

        &.active{

            span{
                background-color: $color-white;
            }
        }

        span{
            display: block;
            width: 32px;
            height: 2px;
            background-color: rgba($color-white, 0.5);
        }
    }
}

// Slider Settings

// About

.about{
    padding: 100px 0;
    text-align: center;

    @include Sm{
        padding: 50px 0;
    }

    h1{
        font-size: 24px;
        font-family: 'frtBlk';
        color: #6d6c6c;
        margin: 0;
        margin-bottom: 35px;

        @include Xs{
            margin-bottom: 20px;
        }
    }

    p{
        font-size: 19px;
        font-family: 'frtLight';
        color: #6d6c6c;
        line-height: 1.789;
        margin: 0;
        margin-bottom: 50px;

        @include Sm{
            margin-bottom: 30px;
        }
    }

    .btns{
        display: flex;
        align-items: center;
        justify-content: center;

        @include mob480{
            flex-direction: column;

            a{
                &:first-child{
                    margin-left: 0;
                    margin-bottom: 20px;
                }
            }
        }
    }

    a{
        position: relative;
        width: 240px;
        height: 55px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: transparent;
        border: 1px solid #a1a1a1;
        font-size: 17px;
        color: #6d6c6c;
        overflow: hidden;
        transition: .5s;
        z-index: 1;

        span{
            z-index: 2;
        }

        &:first-child{
            margin-left: 10px;
            color: #6d6c6c;
            background-color: #6d6c6c;

            &:hover{
                color: #fff;
            }

            &:after, &:before{
                background-color: #fff;
            }

            &:hover{
                &:after{
                    left: -50%;
                }
        
                &:before{
                    right: -50%;
                }
            }
        }

        &:last-child{
            background-color: #6d6c6c;
            color: $color-white;

            &:hover{
                color: #FFF;
                background-color: #8b8b8b;
            }
        }

        &:after, &:before{
            content: "";
            position: absolute;
            top: 0;
            width: 50%;
            height: 100%;
            z-index: -1;
            transition: .5s;
        }

        &:after{
            left: 0%;
        }

        &:before{
            right: 0%;
        }
    }
}

// About

// Video Section

.video{
    position: relative;

    a{
        & > img{
            width: 100%;
        }
    }

    .details{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-47%, -50%);
        font-size: 36px;
        color: #ffffff;
        font-family: 'frtBold';
        width: fit-content;

        img{
            margin: 0 30px;
        }

        @include Md{
            width: 100%;
            text-align: center;
        }

        @include Lg{
            width: auto;
            text-align: unset;
        }

        @include Sm{
            width: 100%;
            text-align: center;
        }

        @include Xs{
            transform: translate(-47%, -50%);
            font-size: 25px;

            img{
                width: 80px;
            }
        }

        @include mob600{
            font-size: 20px;
        }

        @include mob375{
            font-size: 18px;
            transform: translate(-50%, -50%);
            width: 93%;
            text-align: center;

            img{
                width: 50px;
                margin: 0 15px;
            }
        }
    }
}

