// solutions

.solutions{
    padding: 100px 0 30px;

    @include Sm{
        padding: 50px 0 0px;
    }

    @include Xs{
        padding: 50px 0 20px;
    }

    h2{
        font-size: 24px;
        font-family: 'frtBlk';
        margin: 0;
        margin-bottom: 40px;
    }

    .box{
        margin-bottom: 50px;

        @include Xs{
            margin-bottom: 30px;
        }

        .pic{
            box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);   
            width: fit-content;
            margin: auto;
            overflow: hidden;
            
            
            // @include Sm{
                
            // }


            img{
                transition: .5s;
            }
        }

        p{
            text-align: center;
            margin: 0;
            margin-top: 20px;
            font-size: 16px;
            color: #6d6c6c;
            transition: .5s;
        }

        &:hover{
            p{
                color: $color-black;
            }

            img{
                transform: rotate(1deg) scale(1.03);
            }
        }
    }
}

