// catalog

.catalog{
    background-color: #f6f6f6;
    padding: 65px 0;

    @include Sm{
        padding: 10px 0 70px;
    }

    h2{
        font-size: 20px;
        color: rgb(109, 108, 108);
        line-height: 2.6;
        font-weight: 'frtBold';
        width: 100%;          
    }

    .details{
        transform: translate(-50%, -50%);

        img{
            width: 68px;
            height: 68px;
        }
    }

    .video{
        height: 100%;

        a{
            display: block;
            width: 100%;
            height: 100%;
            position: relative;

            & > img{
                height: 100%;
                

                @include Md{
                    height: auto;
                }

                @include Lg{
                    height: 268px !important;
                }
            }
        }
    }

    .content{
        display: flex;
        // align-items: center;
        justify-content: center;
        
        @include Sm{
            flex-wrap: wrap;
            text-align: center;
        }

        .video{
            width: calc(((100% - 15px) / 3));
            margin-left: 30px;

            @include Sm{
                width: 100%;
                margin-left: 0;
                margin-bottom: 30px;
            }

            @include Xs{
                margin-bottom: 10px;
            }

            img{
                @include Sm{
                    width: auto;
                    display: block;
                    margin: auto;
                }
            }
        }

        .boxs{
            width: calc((100% / 3) * 2);

            @include Sm{
                width: 100%;
            }

            .cont{
                // margin-right: 30px;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
    
                @include Xs{
    
                    .owl-dots{
                        bottom: -35px;
    
                        button{
    
                            span{
                                background-color: rgba(0, 0, 0, 0.5);
                            }
    
                            &.active{
    
                                span{
                                    background-color: rgba(0, 0, 0, 0.9);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .box{
        position: relative;
        overflow: hidden;
        width: calc(((100% - 45px) / 4));
        // margin-right: 15px;
        height: max-content;

        &.bottom{
            margin-top: 15px;
        }

        @include Xs{
            // width: calc(((100% - 30px) / 3));
            // margin-left: 15px;
            // margin-bottom: 15px;
            margin: auto;
            width: auto;

            &:nth-of-type(3n){
                margin-left: 0px;
            }

            a{
                display: block;
                width: max-content;
                margin: auto;
                position: relative;
            }

            img{
                width: auto;
            }

            &.bottom{
                margin-top: 0;
            }
        }

        // @include mob600{
        //     width: calc(((100% - 15px) / 2));
        //     margin-left: 15px;
        //     margin-bottom: 15px;

        //     &:nth-of-type(2n){
        //         margin-left: 0px;
        //     }

        //     &:nth-child(3){
        //         margin-left: 15px;
        //     }
        // }

        // @include mob480{
        //     width: 100%;
        //     margin-left: 0;

        //     &:nth-child(3){
        //         margin-left: 0px;
        //     }
        // }

        img{
            width: 100%;
        }

        &:hover{
            .overlay{
                background-color: rgba(#6d6c6c, .5);

                i{
                    left: 0;
                    opacity: 1;
                    transform: scale(1);
                }
            }
        }

        .overlay{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(#6d6c6c, .0);
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;

            i{
                font-size: 24px;
                color: $color-white;
                // position: relative;
                // left: 110px;
                transition: .5s .1s;
                opacity: 0;
                transform: scale(2);
            }
        }
    }
}

// catalog

// footer

footer{
    padding: 58px 0 70px;
    // padding-bottom: 0;
    background-color: #6c6b6b;

    @include Sm{
        padding: 40px 0 30px;
    }

    .foot-links{
        position: relative;

        &:after{
            content: "";
            width: 50px;
            height: 1px;
            display: block;
            background-color: $color-white;
            position: absolute;
            right: 0;
            top: 35px;
        }

        // @include Md{
        //     text-align: center;
        // }

        // @include Lg{
        //     text-align: right;
        // }

        @include Sm{
            margin-bottom: 30px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 18px;
            font-family: "frtBold";
            color: $color-white;
            margin-bottom: 30px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 30px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }

            & > div{
                width: 49%;
                display: inline-block;

                @include mob480{
                    width: initial;
                    display: initial;

                    &:last-child{

                        li{

                            &:first-child{
                                margin-top: 10px;
                            }
                        }
                    }
                }
            }
        }

        a{
            font-size: 16px;
            font-family: "frt";
            color: #cccccc;
            transition: .5s;

            &:hover{
                color: #a7a7a7;
            }
        }
    }

    .contact{


        ul{

            li{

                & > a{
                    display: flex;
                    color: $color-white;

                    .icon{
                        margin-left: 10px;
                        font-size: 14px;

                        i{
                            margin-top: 3px;
                        }
                    }

                    .details{


                        p{
                            &:first-child{
                                font-family: 'frtBold';
                            }

                            &:last-child{
                                color: #cccccc;
                            }
                        }
                    }
                }
            }
        }

        .social{

            a{

                &:not(:last-child){
                    margin-left: 27px;
                }
            }


            i{
                font-size: 20px;
                color: #cccccc;
                transition: .5s;

                &:hover{
                    color: #FFF;
                }
            }
        }
    }
}

// End Of Footer

