            font-size: 14px;
            color: #ccc;
            margin-bottom: 0;

            @include Xs{
                font-size: 13px;
            }
        }

        a{

            @include Xs{
                margin-top: 7px;
            }

            img{
                margin-right: 5px;
            }
        }
    }
}

// End